"use client";
import { useState } from "react";
import Image from "next/image";

// Import your images directly. (Ensure your Next.js config supports these imports.)
import leftImage from "../public/images/IMG_8039.jpg";
import middleImage from "../public/images/IMG_8039.jpg";
import rightImage from "../public/images/IMG_8039.jpg";

const LandingPage: React.FC = () => {
  const [hovered, setHovered] = useState<string | null>(null);

  const handleMouseEnter = (panel: string) => setHovered(panel);
  const handleMouseLeave = () => setHovered(null);

  // Each panel becomes "relative" so that the background image (absolutely positioned)
  // is contained within it.
  const getPanelClass = (panel: string) => {
    const baseClasses =
      "flex-1 relative flex items-center justify-center border transition-all duration-300 cursor-pointer text-xl font-semibold";
    
    if (hovered === null) {
      return `${baseClasses} border-gray-300 text-gray-900`;
    }
    if (hovered === panel) {
      return `${baseClasses} border-blue-500 transform scale-105 shadow-lg text-blue-700`;
    }
    return `${baseClasses} border-gray-300 filter grayscale opacity-50 text-gray-600`;
  };

  return (
    <div className="flex h-screen">
      <div
        className={getPanelClass("left")}
        onMouseEnter={() => handleMouseEnter("left")}
        onMouseLeave={handleMouseLeave}
      >
        {/* Next/Image used as a background image */}
        <Image
          src={leftImage}
          alt="Left Background"
          fill
          className="absolute inset-0 object-cover"
        />
        {/* Content rendered over the background */}
        <div className="relative z-10">Left Panel</div>
      </div>
      <div
        className={getPanelClass("middle")}
        onMouseEnter={() => handleMouseEnter("middle")}
        onMouseLeave={handleMouseLeave}
      >
        <Image
          src={middleImage}
          alt="Middle Background"
          fill
          className="absolute inset-0 object-cover"
        />
        <div className="relative z-10">Middle Panel</div>
      </div>
      <div
        className={getPanelClass("right")}
        onMouseEnter={() => handleMouseEnter("right")}
        onMouseLeave={handleMouseLeave}
      >
        <Image
          src={rightImage}
          alt="Right Background"
          fill
          className="absolute inset-0 object-cover"
        />
        <div className="relative z-10">Right Panel</div>
      </div>
    </div>
  );
};

export default LandingPage;
